import request from '@/common/js/fetch'

export function sendEmail(email,mailType) {
    const data = {
        mailType, //登录：signIn 注册：register 更换邮箱updateEmail等
        email
    }
    return request({
      url: '/sendMail',
      method: 'post',
      params:data
    })
  }


  export function sendSms(mobile,smsType) {
    const data = {
        smsType, //（登录：signIn 注册：register 更换手机号reMobile等）
        mobile
    }
    return request({
      url: '/sendSms',
      method: 'post',
      params:data
    })
  }

  export function verifyCode(params) {
    return request({
      url: '/verifyCode',
      method: 'get',
      params
    })
  }



