import request from '@/common/js/fetch'

export function myStore() {

  return request({
    url: '/store/myStore',
    method: 'get'
  })
}

export function addStore(params) {

  return request({
    url: '/store/add',
    method: 'post',
    params
  })
}



export function commission(params) {

  return request({
    url: '/store/commission ',
    method: 'get',
    params
  })
}



export function nearbyStore(params) {

    return request({
      url: '/store/nearbyStore',
      method: 'get',
      params
    })
  }


export function withdrawCash(params) {

  return request({
    url: '/store/withdrawCash',
    method: 'post',
    params
  })
}

  export function withdrawCashPro(params) {

    return request({
      url: '/store/withdrawCashPro',
      method: 'get',
      params
    })
  }

  export function deleteImg(params) {

    return request({
      url: '/store/deleteImg',
      method: 'get',
      params
    })
  }



  export function updateImg(data) {

    return request({
      url: '/store/updateImg',
      method: 'post',
      data,
      headers:{"Content-Type":"multipart/form-data"}
    })
  }


